const _stackArrayItems = Symbol('stackArrayItems');

export class StackFromArray {
  constructor() {
    this[_stackArrayItems] = [];
  }

  // æ·»å ä¸ä¸ªï¼æå ä¸ªï¼æ°åç´ å°æ é¡¶
  push = (element) => {
    this[_stackArrayItems].push(element);
  }

  // ç§»é¤æ é¡¶çåç´ ï¼åæ¶è¿åè¢«ç§»é¤çåç´ 
  pop = () => {
    return this[_stackArrayItems].pop();
  }

  // è¿åæ é¡¶çåç´ ï¼ä¸å¯¹æ åä»»ä½ä¿®æ¹
  peek = () => {
    return this[_stackArrayItems][this.items.length - 1]
  }

  // å¦ææ éæ²¡æä»»ä½åç´ å°±è¿åtrueï¼å¦åè¿åfalse
  isEmpty = () => {
    // if (this.items.length === 0) return true;
    // return false;
    return this[_stackArrayItems].length === 0;
  }

  // ç§»é¤æ éçææåç´ 
  clear = () => {
    this[_stackArrayItems] = [];
  }

  // è¿åæ éçåç´ ä¸ªæ°ãè¯¥æ¹æ³åæ°ç»çlengthå±æ§å¾ç±»ä¼¼
  size = () => {
    return this[_stackArrayItems].length;
  }
}

 /**
  * éç¨æ°æ®éå¤§ æèä¿®æ¹å¤çæ¶å
  */
// const stackObjItems = new WeakMap();

export class StackFromObj {
  constructor() {
    this.count = 0;
    this.items = {};
  }

  // æ·»å ä¸ä¸ªï¼æå ä¸ªï¼æ°åç´ å°æ é¡¶
  push = (element) => {
    this.items[this.count] = element;
    this.count++;
  }

  // ç§»é¤æ é¡¶çåç´ ï¼åæ¶è¿åè¢«ç§»é¤çåç´ 
  pop = () => {
    if (this.isEmpty()) {
      return undefined;
    }

    this.count--;
    const result = this.items[this.count];
    delete this.items[this.count];
    return result;
  }

  // è¿åæ é¡¶çåç´ ï¼ä¸å¯¹æ åä»»ä½ä¿®æ¹
  peek = () => {
    if (this.isEmpty()) {
      return undefined;
    }

    return this.items[this.count - 1];
  }

  // å¦ææ éæ²¡æä»»ä½åç´ å°±è¿åtrueï¼å¦åè¿åfalse
  isEmpty = () => {
    return this.count === 0;
  }

  // ç§»é¤æ éçææåç´ 
  clear = () => {
    this.items = {};
    this.count = 0;
  }

  // è¿åæ éçåç´ ä¸ªæ°ãè¯¥æ¹æ³åæ°ç»çlengthå±æ§å¾ç±»ä¼¼
  size = () => {
    return this.count;
  }

  toString() {
    if (this.isEmpty()) {
      return '';
    }
    let objString = `${this.items[0]}`; // {1}
    for (let i = 1; i < this.count; i++) { // {2}
      objString = `${objString},${this.items[i]}`; // {3}
    }
    return objString;
  }
}
